.card {
  background:#000000e3;
  border-radius: 5%;
  height: 25%;
  margin: 1rem;
  position: relative;
  width: 15%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:black;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}

.wrapper {
  padding-top: 2%;
  background: rgb(32, 32, 0);
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 2%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: rgb(231, 167, 3);
  margin-bottom: 3%;
}

.navbar {
  width: 23%;
  text-align: left;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: underline;
  color: black;
  background: rgb(255,245,156);
  
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

