.card {
  background:#000000e3;
  border-radius: 5%;
  height: 25%;
  margin: 1rem;
  position: relative;
  width: 15%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:black;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}
